namespace $ { $mol_style_attach( "hyoo/crus/dom/edit/edit.view.css",
 "[hyoo_crus_dom_edit] {\n\tflex-direction: column;\n\tborder-radius: var(--mol_gap_round);\n\tflex: 1;\n\twhite-space: pre-wrap;\n}\n\n[hyoo_crus_dom_edit][contenteditable=\"true\"] {\n\tbackground-color: var(--mol_theme_field);\n\toutline: 1px solid var(--mol_theme_line);\n}\n\n[hyoo_crus_dom_edit]:focus {\n\toutline: none;\n\tz-index: var(--mol_layer_focus);\n\tcolor: var(--mol_theme_text);\n\toutline: 1px solid var(--mol_theme_focus);\n}\n\n[hyoo_crus_dom_edit] a {\n\tcolor: var(--mol_theme_control);\n\ttext-decoration: none;\n}\n\n[hyoo_crus_dom_edit] p {\n\tmargin: 0;\n\tpadding: var(--mol_gap_text);\n}\n\n[hyoo_crus_dom_edit] img {\n\tmargin: var(--mol_gap_block);\n\tmax-width: calc( 100% - 2 * var(--mol_gap_block) );\n}\n\n[hyoo_crus_dom_edit] hr {\n\tmargin: var(--mol_gap_block);\n\tborder: none;\n\tbox-shadow: 0 -1px 0 0px var(--mol_theme_line);\n    height: 1px;\n}\n\n[hyoo_crus_dom_edit] h1,\n[hyoo_crus_dom_edit] h2 {\n\tfont-size: 1em;\n\tfont-weight: bolder;\n\tmargin: 0;\n\tpadding: var(--mol_gap_text);\n}\n"
) }